import React, { useState, useEffect } from "react";
import "./App.css";
import axios from "axios";
import Speech from "react-speech";
import soundfile from "./audio.mp3";

const App = () => {
    const [advice, setAdvice] = useState(
        "Quality is not an act, it is a habit"
    );
    const [doPlay, setDoPlay] = useState(false);
    const [backgroundImg, setBackgroundImg] = useState(
        "https://images.pexels.com/photos/902756/pexels-photo-902756.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"
    );
    const playMusic = () => {
        setDoPlay(!doPlay);
    };
    const fetchAdvice = () => {
        axios
            .get("https://api.adviceslip.com/advice")
            .then((response) => {
                const { advice } = response.data.slip;
                setAdvice(advice);
                console.log(advice.length);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        console.log("component did mount");
        let fetchId = setInterval(fetchAdvice, 200 * advice.length);
        console.log(fetchId);
    }, []);

    return (
        <div
            className="app"
            style={{
                background: `url(${backgroundImg})`,
            }}
        >
            {doPlay && <audio src={soundfile} autoPlay />}
            <div className="card">
                <h1 className="header">{advice}</h1>
                <Speech
                    textAsButton={true}
                    displayText="Narrate"
                    text={advice}
                />
                <button onClick={playMusic}>
                    {doPlay ? "Pause Music" : "Play Music"}
                </button>
            </div>
        </div>
    );
};

export default App;
